.finder__results,
.search__results {
    .card--contact {
        @include search-contact-research-card();
    }
}

.card--contact {
    @include contact-research-card();
}

.related-content {
    .card.card--contact {
        margin-top: 0;
        margin-bottom: 0;
        padding-bottom: $measure--small2;

        & + .card--contact {
            border-top-style: $border-style-solid;
            border-top-color: $color-nearblack;
            border-top-width: $border-width-hairline;
            padding-top: $measure--small2;
        }

        .card__details {
            .card__details__text {
                padding-bottom: $measure--basic;
            }
        }
    }
}
